        var pos = filename.lastIndexOf('/');
        filename = filename.substring(pos + 1);
        pos = filename.indexOf('.');
        filename = filename.substring(0,pos + 1)+'jpg';
        console.log('full',filename)
        filename = require("@/assets/img/thumb/"+filename)
        var arr = []
        arr.push(filename)
        this.previewImgs = arr
        /*
        ImagePreview({
          images: [
            'https://img.yzcdn.cn/vant/apple-1.jpg',
          ],
          closeable: true,
        }); */
      },
    }, 
  }
</script>

